import React, { FC } from "react"
import { Button } from "react-bootstrap"
import { useDispatch } from "react-redux"
import { useNavigate } from "react-router-dom"
import { setCurrentEstate } from "../../store/estatesSlice"
import { IEstate } from "../../store/types/Estate"

interface Props {
    item: IEstate
}


const EstateTableItem: FC<Props> = ({ item }) => {

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const setEstateAndLink = () => {
       dispatch(setCurrentEstate(item))
       navigate("/estates/edit")
    }

    //TODO rewrite with optimization
    const getApartsCountTotal = () => {
        let sum = 0
        item.houses.forEach((h) => {
            h.porches.forEach((p) => {
                sum = sum + (p.floorsTotal * p.apartmentsCount)
            })
        })
        return sum
    }

    const getEndDate = () => {
        let maxYear = 0
        let quartal = 0
        item.houses.forEach((h) => {
            if (maxYear < h.year) {
                maxYear = h.year
                if (quartal < h.quartal) {
                    quartal = h.quartal
                }
            }
        })
        return { maxYear, quartal }
    }


    return (
        <tr>
            <th scope="row">{item.id}</th>
            <td>{item.title}</td>
            <td>
                <p>: {item.developer}</p>
                <p> : {item.houses.length}</p>
                <p> : {getApartsCountTotal()}</p>
                <p> : {getEndDate().quartal} . {getEndDate().maxYear} .</p>
            </td>
            <td>
                <i className="bi bi-house me-2"></i>
                <i className="bi bi-3-circle-fill" />
            </td>
            <td>
                  5%
            </td>
            <td>
                <div className="mb-2">
                    <Button 
                    className="btn btn-default btn-primary mb-2" 
                    onClick={() => setEstateAndLink()}></Button>
                </div>
                <div className="mb-2">
                    <Button className="btn btn-default btn-secondory mb-2">  </Button>
                </div>
                <div className="mb-2">
                    <Button className="btn btn-default btn-info mb-2"> </Button>
                </div>
                <div className="mb-2">
                    <Button className="btn btn-default btn-warning mb-2">  </Button>
                </div>

            
            </td>
        </tr>
    )
}

export default EstateTableItem